<template>
 <v-flex sm12 md10 offset-md1 class=" pt-xs-2 mt-xs-2 pt-sm-4 mt-sm-4 pt-md-8 mt-md-8 pt-4 mt-4" >
  <v-card
  elevation="4" light
  outlined
  >

    <v-progress-linear
      :active="dialogW"
      indeterminate
      color="primary"
    ></v-progress-linear>
    <v-card-title
      class="blue-grey darken-3"
      dark>
        <v-layout align-center justify-space-between>
        <h3 class="text-xs white--text">
            {{ platformName }}
        </h3>
        <v-flex>
            <v-img :alt="platformName" class="ml-3" contain height="48px" position="center right" 
            :src="$baseUrl+'storage/icons/110x92px.png'"
            :lazy-src="$baseUrl+'storage/icons/110x92px.png'"
            ></v-img>
        </v-flex>
        </v-layout>
    </v-card-title>
    <v-divider></v-divider>
      <v-card-text class="pt-0">
          <p>Sign in with your Email and Password:</p>
          <v-form ref="loginForm" v-model="valid" lazy-validation @submit.prevent="validate">
              <v-row class="pt-6">
                  <v-col cols="12">
                      <v-text-field dense outlined  v-model="loginEmail" :rules="loginEmailRules" :error-messages="loginError" label="E-mail" required ></v-text-field>
                  </v-col>
                  <v-col cols="12">
                      <v-text-field dense outlined autocomplete="off" v-model="loginPassword" :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'" :rules="[rules.required]" :type="show1 ? 'text' : 'password'" name="input-10-1" :label="$t('password')"    @click:append="show1 = !show1"></v-text-field>
                  </v-col>
                  <v-col class="d-flex" cols="12" >

                        <v-tooltip bottom class="mt-n1">
                        <template v-slot:activator="{ on }">
                            <a
                            target="_self"
                            href="forgetPassword"
                            @click.stop
                            v-on="on"
                            >
                            {{$t('forgot_password')}}
                            </a>
                        </template>
                        {{$t('use_link_reset')}}
                        </v-tooltip>
                  </v-col>

                  <v-col class="d-flex" cols="12">
                        <router-link to="/register" class="pt-3">
                            {{$t('register_now')}}
                        </router-link>
                  </v-col>
                  <v-spacer></v-spacer>
                  <v-col class="d-flex" cols="12" sm="12" xsm="12" align-end>
                      <v-btn   block :loading="dialogW" :disabled="dialogW"  color="black" class="white--text"  type="submit"> {{$t('login')}} </v-btn>
                  </v-col>
                  <!--
                  <v-spacer></v-spacer>
                  <v-col class="d-flex" cols="12" sm="12" xsm="12" align-end>
                      <v-btn elevation="1"  block :loading="dialogW" :disabled="dialogW" color="grey lighten-3" to="/api/redirect-Social/google"> <v-icon> mdi-google</v-icon> </v-btn>
                  </v-col>
                  -->
              </v-row>
          </v-form>
      </v-card-text>
    <v-divider></v-divider>

    <v-card-actions>
        <lang/>
      <v-spacer></v-spacer>
      <v-btn text
       to="/"
      >
        Cancel
      </v-btn>
    </v-card-actions>

  </v-card>
    <v-layout align-center justify-space-between>
    <p class="caption my-3">
        <a href="https://app.eatinmore.com/privacy">Privacy Policy</a>
        |
        <a href="https://app.eatinmore.com/terms" target="_self">Terms of Service</a>
    </p>
    <p class="caption my-3">Powered by <a href="https://eatinmore.com" target="_self">EatInMore</a></p>
    </v-layout>
</v-flex>
</template>

<script>
import lang from "../Helpers/i18n.vue";
export default {
   components: { lang },
  data() {
    return {
      platformName:"EatInMore",
      dialogW:false,
      loginError:null,
      dialog: true,
      tab: 0,
      tabs: [
          {name:"Login", icon:"mdi-account"},
      ],
      valid: true,
      loginPassword: "",
      loginEmail: "",
      loginEmailRules: [
        v => !!v || "Required",
        v => /.+@.+\..+/.test(v) || "E-mail must be valid"
      ],
      show1: false,
      rules: {
        required: value => !!value || "Required.",
        min: v => (v && v.length >= 8) || "Min 8 characters"
      },
      testError:'',
      country:'',
      user_id:'',
      verMessage:'',
      verUrl:'',
      allerros:'',
      sucess:'',
      verDescription: '',
      verButtonMessage: '',
    };
  },
  beforeCreate() {
    this.form = this.$form.createForm(this, { name: 'normal_login' });
  },
  methods: {

    validate() {
      if (this.$refs.loginForm.validate()) {
        // submit form to server/API here...
        this.dialogW=true;
        this.login(this.loginEmail,this.loginPassword);
      }
    },
    reset() {
      this.$refs.form.reset();
    },
    resetValidation() {
      this.$refs.form.resetValidation();
    },

    login (email,password) {
      this.$store
        .dispatch('login', {
          email: email,
          password: password,
          client_id: 4,
          client_secret: 'wOHYyr9gP8FO812dwySNkCAAJ191zFQmOG5JaaFd',
        })
        .then(() => {
          this.dialogW=false;
          this.$router.push({ name: 'home' })
        })
        .catch(err => {
        this.dialogW=false;
        var status=err.response.status;
        //console.log(status);
            if (status == 403){
                this.user_id=err.response.data.id;
                this.verDescription=err.response.data.description;
                this.verMessage=err.response.data.message;
                this.verUrl=err.response.data.url;
                this.verButtonMessage=err.response.data.button;
                this.openVerifyNotification();
                this.loginError=err.response.data.errors;
            }else if (status == 422){
             const errors = err.response.data;

              //console.log(errors.error);
              this.loginError=errors.errors;
              //this.openNotification('error', 'Error on Save', errors.error);

            }
            else if (status == 429){
             const errors = err.response.data;

              //console.log(errors.errors.email[0]);
              this.loginError=errors.errors.email[0];

            }
            else{
            this.dialogW=false;
            this.openNotification('error','Error during login','Please contact EatInMore Team admin@eatinmore.com');
          }
        })
    },

    openNotification: function (type, m, d) {
        this.$notification.config({
            placement: 'topRight',
            top: 35,
            duration: 8,
        });
        this.$notification[type]({
          message: m,
          description: d,
        });
    },
    closeNotification(key){
        this.axios
        .post(this.verUrl, { data: { id: this.user_id} })
        .then(response => {
            this.allerros = [];
            this.sucess = true;
            if (response.data.errors) {
                response.data.errors.forEach(error => { this.openNotification('error', 'Action Button Error', error);});

            } else {

                this.openNotification('success', this.verMessage, 'Action Button successfully');
            }
        })
      .catch((error) => {
        this.success = false;
        var errors =null;
        var status=error.response.status;
            if (status == 422){
            errors=error.response.data.errors;
            errors.forEach(error => { this.openNotification('error', 'Action Button Error', error);});
          }else{
            this.openNotification('error','Action Button Error',error.response.data['error']);
          }
      });

        this.$notification.close(key)
    },
    openVerifyNotification() {
      const key = `open${Date.now()}`;
      this.$notification.open({
        message: this.verMessage,
        placement:'bottomRight',
        description:
           this.verDescription,
        btn: h => {
          return h(
            'a-button',
            {
              props: {
                type: 'primary',
                size: 'small',
              },
              on: {
                click: () => this.closeNotification(key),
              },
            },
            this.verButtonMessage,
          );
        },
        key,
        onClose: close,
        duration: 0,

      });
    },

  },
};
</script>
<style>
#components-form-demo-normal-login .login-form {
  max-width: 300px;
}
#components-form-demo-normal-login .login-form-forgot {
  float: right;
}
#components-form-demo-normal-login .login-form-Remember {
  float: left ;
}
#components-form-demo-normal-login .login-form-button {
  width: 100%;
}
</style>
